import { useState } from 'react';
import { Box, Button, makeStyles, Typography } from '@material-ui/core';

import manifestoImg from 'assets/images/manifesto.png';
import { MintModal } from './MintModal';

const useStyles = makeStyles(() => ({
	container: {
		position: 'relative',
		width: '100%',
		height: '100%',
		display: 'flex',
		flexDirection: 'column',
		alignItems: 'center',
		justifyContent: 'space-between',
		boxSizing: 'border-box',
	},
	image: {
		height: 'calc(100% - 150px)',
		maxWidth: '100%',
	},
	button: {
		marginBottom: 50,
	},
	footer: {
		position: 'absolute',
		right: 0,
		bottom: 0,
		color: 'grey',
	},
}));

export const Home = () => {
	const classes = useStyles();
	const [showModal, setShowModal] = useState(false);

	return (
		<>
			<Box className={classes.container}>
				<img src={manifestoImg} alt="" className={classes.image} />

				<Button variant="contained" color="primary" className={classes.button} onClick={() => setShowModal(true)}>
					Mint Manifesto
				</Button>

				<Typography className={classes.footer}>Launch by Medici</Typography>
			</Box>

			<MintModal open={showModal} onClose={() => setShowModal(false)} />
		</>
	);
};
