import { Box, Button, makeStyles, Modal } from '@material-ui/core';
import { useNft, useWallet } from 'contexts';
import { useState } from 'react';
import { WalletType } from 'types';

const useStyles = makeStyles(theme => ({
	container: {
		position: 'fixed',
		top: '50%',
		left: '50%',
		transform: 'translate(-50%, -50%)',
		width: 300,
		padding: 20,
		display: 'flex',
		alignItems: 'center',
		justifyContent: 'space-between',
		backgroundColor: theme.palette.background.default,
		borderRadius: 10,
	},
	disabledButton: {
		cursor: 'not-allowed !important',
		backgroundColor: 'grey !important',
		color: 'white !important',
	},
}));

interface IMintModal {
	open: boolean;
	onClose: () => void;
}

export const MintModal: React.FC<IMintModal> = ({ open, onClose }) => {
	const classes = useStyles();
	const { connected, connect } = useWallet();
	const { mint } = useNft();

	const [loading, setLoading] = useState(false);

	const handleMint = async () => {
		setLoading(true);
		await mint('0x123456');
		setLoading(false);
	};

	return (
		<Modal open={open} onClose={() => !loading && onClose()}>
			<Box className={classes.container}>
				<Button
					variant="contained"
					color="primary"
					classes={{ disabled: classes.disabledButton }}
					disabled={connected || loading}
					onClick={() => connect(WalletType.MetaMask)}
				>
					Connect Wallet
				</Button>

				<Button
					variant="contained"
					color="primary"
					classes={{ disabled: classes.disabledButton }}
					disabled={!connected || loading}
					onClick={handleMint}
				>
					Press Mint
				</Button>
			</Box>
		</Modal>
	);
};
